import React, { Component } from 'react';
import "./Movie.css"

class Movie extends Component{
  constructor(props){
    super(props);
    this.state={
      id: this.props.id,
      year: this.props.year,
      title: this.props.title,
      summary: this.props.summary,
      poster: this.props.poster,
      genres: this.props.genres
    }
  }
  render(){
    return(
      <content>
        <div className="movie">
          <img src={this.state.poster} alt={this.state.title} title={this.state.title}></img>
          <div className="movie_data">
            <h3 className="movie_title">{this.state.title}</h3>
            <h5 className="movie_year">{this.state.year}</h5>
            <ul className="genres">{this.state.genres.map((genre, index) => <li key={index} className="genres_genre">{genre}</li>)}</ul>
            <p className="movie_summary">{this.state.summary.slice(0,180)}...</p>
          </div>
        </div>
      </content>
    );
  }
}

export default Movie;